import { Component, OnInit } from '@angular/core';
import { WeatherService } from './weather/weather.service';
import { createAnimation } from "@ionic/core";
import { ModalController } from '@ionic/angular';
import { AlertController } from '@ionic/angular';
import { Button } from 'selenium-webdriver';
import { HttpClient } from '@angular/common/http';




@Component({
  selector: 'app-weather',
  templateUrl: './weather.page.html',
  styleUrls: ['./weather.page.scss'],
})


export class WeatherPage implements OnInit {

  lat: any;
  lon: any;
  weather: any;
  icon: any;
  iconSearch: any;
  searchWeather: any;
  today = new Date();
  jstoday = '';
  now: string;
  // eslint-disable-next-line @typescript-eslint/naming-convention
  CitysArray: any[] = [];

  url = 'https://api.openweathermap.org/data/2.5/weather?&units=metric';

  openClose = false;

  public pageInfo = {title:'Météo', subTitle: '', };
  constructor(private weatherService: WeatherService,
    public alertController: AlertController,
    private http: HttpClient) {
    setInterval(() => {
      this.now = new Date().toString().split(' ')[4];
    }, 1);
  }

  async introuvableAlert() {
    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: '',
      subHeader: '',
      message: 'Entrez une ville',
      buttons: [
        {
          text: 'Ok',
        }
      ]
    });
    await alert.present();
  }
  async notFoundAlert(city) {
    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: '',
      subHeader: '',
      message: city + ' : ' + 'Ville introuvable',
      buttons: [
        {
          text: 'Ok',

        }
      ]
    })
  }

  // eslint-disable-next-line @typescript-eslint/naming-convention
  ParisWeather() {
    this.weatherService.parisWeather().subscribe(data => {
      this.weather = data;
      switch (this.weather.weather[0].description) {
        case 'overcast clouds':
          this.icon = "cloud-outline";
          break;
        case 'clear sky':
          this.icon = "sunny-outline";
          break;
        case 'few clouds':
          this.icon = "partly-sunny-outline";
          break;
        case 'broken clouds':
          this.icon = "cloud-outline";
          break;
        case 'shower rain':
          this.icon = "rainy-outline";
          break;
        case 'rain':
          this.icon = "thunderstorm-outline";
          break;
        case 'thunderstorm':
          this.icon = "rainy-outline";
          break;
        case 'snow':
          this.icon = "snow-outline";
          break;
        case 'mist':
          this.icon = "cloud-outline";
          break;
        default:
          this.icon = "partly-sunny-outline";
      }
    });
  }

  addCity(city: any) {
    switch (city.weather[0].description) {
      case 'overcast clouds':
        city.weather[0].description = "cloud-outline";
        break;
      case 'clear sky':
        city.weather[0].description = "sunny-outline";
        break;
      case 'few clouds':
        city.weather[0].description = "partly-sunny-outline";
        break;
      case 'broken clouds':
        city.weather[0].description = "cloud-outline";
        break;
      case 'shower rain':
        city.weather[0].description = "rainy-outline";
        break;
      case 'rain':
        city.weather[0].description = "thunderstorm-outline";
        break;
      case 'thunderstorm':
        city.weather[0].description = "rainy-outline";
        break;
      case 'snow':
        city.weather[0].description = "snow-outline";
        break;
      case 'mist':
        city.weather[0].description = "cloud-outline";
        console.log("mist")
        break;
      default:
        city.weather[0].description = "partly-sunny-outline";
    }
    this.CitysArray.push(city);
  }

  handleError(error, city) {
    if (error.status == 404) {
      this.notFoundAlert(city);
    }
  }

  getCity(city) {
    if (city.value !== '') {
      this.weatherService.getWeatherDataByCityName(city.value).subscribe(data => {
        this.searchWeather = data;
        this.addCity(this.searchWeather);
      }, (error) => this.handleError(error, city.value))
    } else {
      this.introuvableAlert();
    }
  }

  openBottomSheet() {
    const animation = createAnimation()
      .addElement(document.querySelector(".bottomSHeet"))
      .easing("ease-in-out")
      .duration(400)
      .direction("alternate")
      .keyframes([
        { height: "0px", transform: "scale(1)", opacity: "1", },
        { height: "15rem", transform: "scale(1)", opacity: "1" },
      ]);
    (<HTMLStyleElement>document.querySelector(".bottomSHeet")).style.display = "block";
    this.openClose = true;
    animation.play();
  }

  closeBottomSheet() {
    const animation = createAnimation()
      .addElement(document.querySelector(".bottomSHeet"))
      .easing("ease-in-out")
      .duration(300)
      .direction("alternate")
      .keyframes([
        { height: "15rem", },
        { height: "0px", },
      ]);
    this.openClose = false;
    animation.play();
  }

  bottomShet() {
    if (this.openClose) {
      this.closeBottomSheet();
    } else {
      this.openBottomSheet();
    }
  }

  ngOnInit() {
    (<HTMLStyleElement>document.querySelector(".bottomSHeet")).style.display = "none";
    this.ParisWeather()
  }
}

